/**
 *
 * Sin Wave Cone
 * @author Peter Laxalt
 * @description Simple Sin Wave Cone in p5.js
 *
 */

import p5 from "p5";

// Begin Component
// __________________________________________________________________________

export default (W: number, H: number, BG: string) => (p: p5) => {
  let x = 0;
  let scale = 0;

  let color = {
    primary: "black",
    primaryAlpha: "black",
    secondary: "#000000",
    bg: BG,
    white: "#ffffff",
  };

  p.setup = () => {
    // Our Canvas
    p.createCanvas(W, H, p.WEBGL);
    p.noStroke();
  };

  p.draw = () => {
    scale += 1;
    scale;
    x = x * 1.1 + 2;

    p.noFill();
    p.stroke(color.primary);
    p.background(color.bg);

    for (let i = 0; i < 20; i++) {
      p.cone(300, 400);
      p.stroke(color.primaryAlpha);
      p.strokeWeight(1);
      p.translate(
        parseInt((Math.sin(p.mouseX * 0.001) * 50).toString()),
        parseInt((Math.sin(p.mouseY * 0.001) * 50).toString()),
        parseInt((Math.sin(p.mouseX * 0.001) * 50).toString())
      );
      p.smooth();
      p.rotateX(p.frameCount * 0.01);
      p.rotateY(p.frameCount * 0.01);
    }
  };

  p.windowResized = () => {
    p.resizeCanvas(W, H);
    p.background("yellow");
  };
};
